	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>Zoolog is an experience that encourages children to become more active through exploring the world’s fauna. The prototype models a journey for young to middle aged children, living in suburban and city areas. The user earns badges by collecting photographic observations of wildlife. Through their journey, prompts and tasks are also suggested to inspire more exploration. “Zoolog” is a wordplay on Zoology, the study of animals.</p>\n<p>![Image of the Zoolog app being used outdoors by a child.](../assets/screen shot 2018-01-22 at 12.46.50.png)</p>\n<p>![A spread of the Zoolog app screens in Invision.](../assets/screen shot 2018-01-22 at 13.51.53.png)</p>","frontmatter":{"title":"Zoolog Children's App","graduate":"My Van Loc","category":"UI/UX","category2":"Product Design","category3":"Experiential Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/screen shot 2018-01-22 at 12.44.57-a3aab1d74ff152d650eedcf4047480f8-a7a62.jpg","srcSet":"/static/screen shot 2018-01-22 at 12.44.57-a3aab1d74ff152d650eedcf4047480f8-5a1a9.jpg 375w,\n/static/screen shot 2018-01-22 at 12.44.57-a3aab1d74ff152d650eedcf4047480f8-0908c.jpg 750w,\n/static/screen shot 2018-01-22 at 12.44.57-a3aab1d74ff152d650eedcf4047480f8-a7a62.jpg 1500w,\n/static/screen shot 2018-01-22 at 12.44.57-a3aab1d74ff152d650eedcf4047480f8-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/zoolog-children-s-app/","name":"/Zoolog Children's App/"}}

